export default "import React from 'react';\nimport {\n    ComponentsRepo,\n    changeEventHandler,\n    AutoFields,\n    RepositoryComponentByType,\n    AutoViewProps,\n    useRepositoryContext\n} from '@autoviews/core';\nimport {\n    FormControl,\n    Select,\n    MenuItem,\n    InputLabel,\n    TextField,\n    Box\n} from '@mui/material';\n\nconst customIfType = Symbol('if/then/else');\n\nexport const repo = new ComponentsRepo('conditions-repo', node => {\n    if ('type' in node) {\n        return node.type;\n    }\n\n    if ('oneOf' in node) {\n        return 'oneOf';\n    }\n\n    throw new Error('cannot resolve type');\n});\n\nconst ObjectComponent: React.FC<AutoViewProps> = props => (\n    <>\n        <AutoFields {...props} />\n        {props.schema.if && (\n            <RepositoryComponentByType\n                type={customIfType}\n                {...props}\n            />\n        )}\n    </>\n);\n\nconst OneOfAsEnumComponent: React.FC<AutoViewProps> = props => (\n    <FormControl fullWidth>\n        <InputLabel id=\"select-label\">{props.schema.title}</InputLabel>\n        <Select\n            labelId=\"select-label\"\n            id=\"select\"\n            value={props.data}\n            label={props.schema.title}\n            onChange={changeEventHandler(props, e => e.target.value)}\n        >\n            {props.schema.oneOf!.map(item => (\n                <MenuItem\n                    value={item.const}\n                    key={item.const}\n                >\n                    {item.title}\n                </MenuItem>\n            ))}\n        </Select>\n    </FormControl>\n);\n\nconst IfThenElseComponent: React.FC<AutoViewProps> = ({\n    schema,\n    data,\n    ...otherProps\n}) => {\n    const {validator} = useRepositoryContext();\n\n    const {if: ifStatement, then: thenStatement, else: elseStatement} = schema;\n\n    if (!ifStatement || !thenStatement) {\n        throw new Error(\n            'IfThenElseComponent cannot be invoked without `if` and `then` properties in schema'\n        );\n    }\n\n    if (validator.compile(ifStatement)(data)) {\n        return (\n            <AutoFields\n                {...otherProps}\n                data={data}\n                schema={{type: 'object', ...thenStatement}}\n                validation={false}\n            />\n        );\n    }\n\n    if (elseStatement) {\n        return (\n            <AutoFields\n                {...otherProps}\n                data={data}\n                schema={elseStatement}\n                validation={false}\n            />\n        );\n    }\n\n    return null;\n};\n\nrepo.register('object', {\n    name: 'object',\n    component: ObjectComponent\n})\n    .register('number', {\n        name: 'numberInput',\n        component: ({schema}) => (\n            <Box sx={{margin: '20px 0'}}>\n                <TextField\n                    id=\"cats_qty\"\n                    type=\"number\"\n                    variant=\"outlined\"\n                    label={schema.title}\n                />\n            </Box>\n        )\n    })\n    .register(customIfType, {\n        name: 'if/then/else',\n        component: IfThenElseComponent\n    })\n    .register('oneOf', {\n        name: 'oneOfAsEnum',\n        component: OneOfAsEnumComponent\n    });\n";